.announce-detail {
  margin-top: 5px;
}
.announce-detail .detail-return {
  padding-bottom: 15px;
  border-bottom: 1px solid #e7e7e7;
}
.announce-detail .deatil-header {
  margin-top: 20px;
}
.announce-detail .deatil-header .header-tital {
  font-weight: 500;
  font-size: 28px;
  color: #333333;
  letter-spacing: 0;
  line-height: 32px;
  margin: 0;
}
.announce-detail .deatil-header .header-time {
  font-size: 12px;
  color: #333333;
  letter-spacing: 0;
  margin-top: 8px;
  margin-bottom: 20px;
}
.announce-detail .deatil-img {
  width: 100%;
}
.announce-detail .deatil-img img {
  width: 80%;
}
.announce-detail .deatil-content {
  width: 100%;
  margin-top: 20px;
}
.announce-detail .deatil-content img {
  width: 100%;
}
@media only screen and (max-width: 768px) {
  .announce-detail .deatil-header {
    margin-top: 20px;
  }
  .announce-detail .deatil-header .header-tital {
    font-size: 18px;
  }
}
